#workshops {
    background-color: var(--light2);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 80px;
}

.cards {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 80vw;
    padding-bottom: 50px;
}

.card {
    color: var(--dark2);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 1.7rem;
    box-shadow: 0 4px 8px 0 var(--accent), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    border-radius: 2px;
    width: 380px;
    margin: 25px 10px;
}

.time {
    font-family: var(--primary-font);
    text-transform: uppercase;
    color: gray;
    font-size: 13px;
}

.card-title {
    font-family: var(--primary-font);
    display: flex;
    justify-content: center;
    align-items: center;
    vertical-align: middle;
    text-align: center;
    font-size: 20px;
    height: 90px;
}

.card-speakers {
    display: flex;
    text-align: right;
    font-family: var(--primary-font);
    font-size: 16px;
    margin-bottom: 0;
}

.card-description {
    vertical-align: middle;
    text-align: justify;
}

.inviteCards .card {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 80vw;
}

.inviteCards .card-title-section {
    display: flex;
    flex-direction: column;
    width: 50%;
    padding: 1rem;
}

.vertical {
    border: 0;
    border-left: 1px solid rgba(0,0,0,.1);
    height: 200px;
    width: 30%;
    margin-left: 1rem;
    margin-right: 1rem;
}

.horizontal {
    display: none;
}

.inviteCards .card-link {
    display: flex;
    justify-content: center;
    width: 50%;
    padding: 1rem;
}
