#our-team {
    background-color: var(--dark2);
    color: var(--light2);
    display: flex;
    flex-direction: column;
}

#our-team .title {
    text-transform: uppercase;
}

#our-team .subtitle {
    font-family: var(--primary-font);
    padding: 2vh;
    text-transform: uppercase;
}

.membersList {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    padding: 30px;
}

.teamMember {
    font-family: var(--primary-font);
    text-transform: uppercase;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: 20px;
}

.memberImage {
    width: 300px;
    height: auto;
    padding: 20px;
}

.collaboratorsList {
    font-family: var(--primary-font);
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    padding: 1vh 10vw 1vh;
}

.collaboratorName {
    padding: 1vw 3vh;
    text-align: center;
}
