#contact {
    background-color: var(--dark2);
    padding: 10px 50px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
}

.contactLinks {
    font-size: 20px;
    display: flex;
    flex-direction: column;
    padding-top: 10px;
    padding-bottom: 10px;
}

.socialMedia {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.contactSlot {
    color: var(--light2);
}

.icon {
    margin: 10px;
}

.contactMail {
    color: var(--light2);
    display: flex;
    align-items: center;
    justify-content: center;
}

.contactLink {
    margin: 10px;
}

.contactMail {
    display: flex;
    justify-content: space-around;
}

.contactMail:hover {
    text-decoration: none;
}

.footerLogo {
    display: flex;
    justify-content: center;
    align-items: center;
}

.logoUB {
    height: 60px;
    margin: 10px;
}
