#winners {
    background-color: var(--light2);
    color: var(--dark2);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}

.podium {
    display: grid;
    grid-auto-flow: column dense;
    gap: 5vw;
    margin-top: 2rem;
    justify-content: center;
    justify-items: center;
    align-content: flex-end;
    align-items: flex-end;
    border-bottom: 1px solid #e5e7eb;
    height: 250px;
}

.winner-card {
    background-color: var(--light2);
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    align-items: center;
    border: none;
}

.winner-card:hover {
    color: rgba(0, 0, 0, 0.4);
}

.winner-team {
    font-size: 1.5rem;
}

.winner-members {
    padding-top: -2vw;
    font-size: 1rem;
}

.center {
    text-align: center;
}

.podium-step {
    display: flex;
    flex-direction: column;
    place-content: center;
}
