#conferences {
    background-color: var(--accent);
    color: var(--light2);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 80px;
}

#conferences .button {
    justify-content: flex-start;
    margin: 0;
}

#conferences .button-link:not(:hover) {
    background-color: var(--light2);
    color: var(--accent);
}

#conferences .details {
    padding-top: 40px;
    padding-bottom: 40px;
}

.conference-card {
    display: flex;
    flex-direction: row;
}

.conference-time {
    font-family: var(--primary-font);
    text-transform: uppercase;
    display: flex;
    text-align: center;
    vertical-align: middle;
    justify-content: center;
    align-items: center;
    font-size: 13px;
    padding: 20px;
    border-right: 1px solid var(--light1);
    color: var(--light3);
    margin: 0;
    width: 70px;
}

.conference-info {
    padding: 20px;
}

.conference-title {
    font-size: 23px;
    padding-bottom: 10px;
}

.conference-speaker {
    font-family: var(--primary-font);
    color: var(--light2);
    font-size: 17px;
}

.conference-details {
    font-family: var(--primary-font);
    color: var(--light3);
    font-size: 15px;
}

.conference-line {
    border-top: 1px solid var(--light1);
    margin: 0;
}

.conference-link {
    font-size: 1.2rem;
}

#conferences p {
    font-size: 20px;
}
