:root {
    --dark2: black;
    --light1: rgba(236, 236, 236, .3);
    --light2: white;
    --light3: rgba(242, 241, 239, .8);
    --accent: #401365;
    --highlight: #F8D911;
    --schedule-color-1: var(--accent);
    --schedule-color-2: purple;
    --primary-font: 'Lexend Mega', sans-serif;
    --secondary-font: 'Nunito Sans' sans-serif;
}

body {
    width: 100%;
    font-family: var(--secondary-font);
}

.page {
    min-height: 100vh;
    display: flex;
    align-items: center;
    padding-top: 15vh;
    padding-bottom: 10vh;
}

.title {
    font-family: var(--primary-font);
    font-size: 4rem;
    text-align: center;
    padding-left: 20px;
    padding-right: 20px;
}

.date {
    font-family: var(--primary-font);
    text-transform: uppercase;
}

.details {
    width: 80vw;
    padding-top: 20px;
}

ul {
    list-style: none;
    padding-left: 0;
    padding-top: 2vh;
    font-size: 1.2rem;
}

li {
    text-align: justify;
    padding: 0.5vh;
}

.button {
    display: flex;
    justify-content: center;
    padding-top: 20px;
    margin: auto;
}

.button-link {
    background-color: var(--accent);
    color: var(--light);
    font-size: 1.3rem;
    padding: 8px 30px;
    text-transform: uppercase;
    border-radius: 4px;
    transition-duration: 0.3s;
}

.button-link:hover {
    background: var(--schedule-color-2);
    text-decoration: none;
    color: var(--light2);
}
