.nav-color {
    background-color: var(--dark2);
}

.navbar-brand {
    padding: 10px;
}

.nav-items {
    display: flex;
    flex-direction: column;
}

.nav-item {
    padding: 10px;
}

#home {
    background: url(/static/media/fundal-home-page.ec4f2460.jpg) no-repeat center center fixed;
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
}

#home .box {
    padding: 3rem;
    background-color: white;
    box-shadow: 0 4px 8px 0 var(--accent), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    border-radius: 2px;
}

#home .title {
    text-align: left;
}

#subtitle {
    font-size: 2rem;
    text-align: center;
}

#first-page.button {
    background: var(--accent);
}

#about-us {
    background-color: var(--dark2);
    color: var(--light2);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 10px;
}

#about-us .box {
    background-color: var(--accent);
    box-shadow: 0 4px 0 0 var(--light2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 70vw;
}

#about-us .title {
    font-size: 2.5rem;
}

#about-us ul {
    padding-top: 2vw;
    font-size: 1.2rem;
}

#about-us li {
    padding: 1vw;
}

#schedule {
    background: var(--dark2);
    display: flex;
    flex-direction: column;
    padding-left: 10px;
    padding-right: 10px;
}

#schedule .title {
    color: var(--light2);
    padding-bottom: 5vh;
    text-transform: uppercase;
}

#schedule .date {
    color: var(--light2);
}

.vertical-timeline {
    padding-top: 5vh;
}

.scheduleIcon {
    background: var(--schedule-color-1);
    color: var(--light2);
}

#workshops {
    background-color: var(--light2);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 80px;
}

.cards {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 80vw;
    padding-bottom: 50px;
}

.card {
    color: var(--dark2);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 1.7rem;
    box-shadow: 0 4px 8px 0 var(--accent), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    border-radius: 2px;
    width: 380px;
    margin: 25px 10px;
}

.time {
    font-family: var(--primary-font);
    text-transform: uppercase;
    color: gray;
    font-size: 13px;
}

.card-title {
    font-family: var(--primary-font);
    display: flex;
    justify-content: center;
    align-items: center;
    vertical-align: middle;
    text-align: center;
    font-size: 20px;
    height: 90px;
}

.card-speakers {
    display: flex;
    text-align: right;
    font-family: var(--primary-font);
    font-size: 16px;
    margin-bottom: 0;
}

.card-description {
    vertical-align: middle;
    text-align: justify;
}

.inviteCards .card {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 80vw;
}

.inviteCards .card-title-section {
    display: flex;
    flex-direction: column;
    width: 50%;
    padding: 1rem;
}

.vertical {
    border: 0;
    border-left: 1px solid rgba(0,0,0,.1);
    height: 200px;
    width: 30%;
    margin-left: 1rem;
    margin-right: 1rem;
}

.horizontal {
    display: none;
}

.inviteCards .card-link {
    display: flex;
    justify-content: center;
    width: 50%;
    padding: 1rem;
}

#conferences {
    background-color: var(--accent);
    color: var(--light2);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 80px;
}

#conferences .button {
    justify-content: flex-start;
    margin: 0;
}

#conferences .button-link:not(:hover) {
    background-color: var(--light2);
    color: var(--accent);
}

#conferences .details {
    padding-top: 40px;
    padding-bottom: 40px;
}

.conference-card {
    display: flex;
    flex-direction: row;
}

.conference-time {
    font-family: var(--primary-font);
    text-transform: uppercase;
    display: flex;
    text-align: center;
    vertical-align: middle;
    justify-content: center;
    align-items: center;
    font-size: 13px;
    padding: 20px;
    border-right: 1px solid var(--light1);
    color: var(--light3);
    margin: 0;
    width: 70px;
}

.conference-info {
    padding: 20px;
}

.conference-title {
    font-size: 23px;
    padding-bottom: 10px;
}

.conference-speaker {
    font-family: var(--primary-font);
    color: var(--light2);
    font-size: 17px;
}

.conference-details {
    font-family: var(--primary-font);
    color: var(--light3);
    font-size: 15px;
}

.conference-line {
    border-top: 1px solid var(--light1);
    margin: 0;
}

.conference-link {
    font-size: 1.2rem;
}

#conferences p {
    font-size: 20px;
}

#hackathon {
    background-color: var(--light2);
    display: flex;
}

#hackathon .container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#hackathon .info {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

#hackathon .info .detailsList {
    width: 25vw;
}

.detailsList {
    padding-top: 25px;
    padding-bottom: 25px;
}

#hackathon .detailsTitle {
    text-align: center;
}

#hackathon .button {
    justify-content: flex-start;
    margin: 0;
}

#hackathon .button-link:not(:hover) {
    background-color: var(--accent);
    color: var(--light2);
}

#winners {
    background-color: var(--light2);
    color: var(--dark2);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}

.podium {
    display: grid;
    grid-auto-flow: column dense;
    grid-gap: 5vw;
    gap: 5vw;
    margin-top: 2rem;
    justify-content: center;
    justify-items: center;
    align-content: flex-end;
    align-items: flex-end;
    border-bottom: 1px solid #e5e7eb;
    height: 250px;
}

.winner-card {
    background-color: var(--light2);
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    align-items: center;
    border: none;
}

.winner-card:hover {
    color: rgba(0, 0, 0, 0.4);
}

.winner-team {
    font-size: 1.5rem;
}

.winner-members {
    padding-top: -2vw;
    font-size: 1rem;
}

.center {
    text-align: center;
}

.podium-step {
    display: flex;
    flex-direction: column;
    place-content: center;
}

#our-team {
    background-color: var(--dark2);
    color: var(--light2);
    display: flex;
    flex-direction: column;
}

#our-team .title {
    text-transform: uppercase;
}

#our-team .subtitle {
    font-family: var(--primary-font);
    padding: 2vh;
    text-transform: uppercase;
}

.membersList {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    padding: 30px;
}

.teamMember {
    font-family: var(--primary-font);
    text-transform: uppercase;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: 20px;
}

.memberImage {
    width: 300px;
    height: auto;
    padding: 20px;
}

.collaboratorsList {
    font-family: var(--primary-font);
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    padding: 1vh 10vw 1vh;
}

.collaboratorName {
    padding: 1vw 3vh;
    text-align: center;
}

#partners {
    background-color: var(--light2);
    color: var(--dark2);
    display: flex;
    flex-direction: column;
    justify-content: center;
    letter-spacing: 2px;
    margin-left: 50px;
    margin-right: 50px;
}

#partners .title {
    text-transform: uppercase;
}

.sponsorList {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    padding-top: 50px;
}

.sponsor {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 30px;
}

.sponsorImage {
    height: 70px;
    width: auto;
}

.partnerList {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    padding-bottom: 50px;
}

.partnerImage {
    height: 55px;
    width: auto;
}

#partners .button {
    margin: 10px;
    padding: 2px;
}

#contact {
    background-color: var(--dark2);
    padding: 10px 50px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
}

.contactLinks {
    font-size: 20px;
    display: flex;
    flex-direction: column;
    padding-top: 10px;
    padding-bottom: 10px;
}

.socialMedia {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.contactSlot {
    color: var(--light2);
}

.icon {
    margin: 10px;
}

.contactMail {
    color: var(--light2);
    display: flex;
    align-items: center;
    justify-content: center;
}

.contactLink {
    margin: 10px;
}

.contactMail {
    display: flex;
    justify-content: space-around;
}

.contactMail:hover {
    text-decoration: none;
}

.footerLogo {
    display: flex;
    justify-content: center;
    align-items: center;
}

.logoUB {
    height: 60px;
    margin: 10px;
}

:root {
    --dark2: black;
    --light1: rgba(236, 236, 236, .3);
    --light2: white;
    --light3: rgba(242, 241, 239, .8);
    --accent: #401365;
    --highlight: #F8D911;
    --schedule-color-1: var(--accent);
    --schedule-color-2: purple;
    --primary-font: 'Lexend Mega', sans-serif;
    --secondary-font: 'Nunito Sans' sans-serif;
}

body {
    width: 100%;
    font-family: 'Nunito Sans' sans-serif;
    font-family: var(--secondary-font);
}

.page {
    min-height: 100vh;
    display: flex;
    align-items: center;
    padding-top: 15vh;
    padding-bottom: 10vh;
}

.title {
    font-family: 'Lexend Mega', sans-serif;
    font-family: var(--primary-font);
    font-size: 4rem;
    text-align: center;
    padding-left: 20px;
    padding-right: 20px;
}

.date {
    font-family: 'Lexend Mega', sans-serif;
    font-family: var(--primary-font);
    text-transform: uppercase;
}

.details {
    width: 80vw;
    padding-top: 20px;
}

ul {
    list-style: none;
    padding-left: 0;
    padding-top: 2vh;
    font-size: 1.2rem;
}

li {
    text-align: justify;
    padding: 0.5vh;
}

.button {
    display: flex;
    justify-content: center;
    padding-top: 20px;
    margin: auto;
}

.button-link {
    background-color: #401365;
    background-color: var(--accent);
    color: var(--light);
    font-size: 1.3rem;
    padding: 8px 30px;
    text-transform: uppercase;
    border-radius: 4px;
    transition-duration: 0.3s;
}

.button-link:hover {
    background: purple;
    background: var(--schedule-color-2);
    text-decoration: none;
    color: white;
    color: var(--light2);
}

@media (max-width: 1500px) {
    .cards {
        justify-content: space-around;
    }

    .card {
        width: 450px;
    }
}

@media (max-width: 1200px) {
    .card {
        width: 400px;
    }
}

@media (max-width: 1100px) {
    #about-us .box {
        width: 90vw;
    }

    .card {
        width: 450px;
    }

    #hackathon .info {
        flex-direction: column;
    }

    #hackathon .info .detailsList {
        width: auto;
    }
}

@media (max-width: 768px) {
    #NavBar:not(.hidden) {
        position: relative;
        flex-direction: column;
        align-items: center;
        justify-content: flex-start;
        background: var(--dark2);
        z-index: 10;
        width: 100vw;
    }

    .box {
        padding: 6vw;
    }

    .title {
        font-size: 8vw;
    }

    #subtitle, .subtitle {
        font-size: 4vw;
    }

    .button-link {
        font-size: 2.5vw;
    }

    #hackathon h4 {
        font-size: 3vw;
    }

    #workshops .date {
        font-size: 3vw;
    }

    .inviteCards .card {
        flex-direction: column;
        width: 450px;
    }

    .inviteCards .card-title-section {
        width: auto;
    }

    .vertical {
        display: none;
    }

    .horizontal {
        display: inline-block;
    }

    #conferences h4 {
        font-size: 3vw;
    }

    #winners h4 {
        font-size: 3vw;
    }

    .vertical-timeline-element-title {
        font-size: 20px;
    }

    .vertical-timeline-element-content p {
        font-size: 15px;
    }

    #partners h4 {
        font-size: 3vw;
        padding: 2vw;
        text-align: center;
    }
}

@media (max-width: 650px) {
    .inviteCards .card {
        width: 80vw;
    }

    .winners-podium {
        display: flex;
        flex-direction: column;
    }

    .gray1, .gray2, .gray3 {
        display: none;
    }

    #contact {
        justify-content: center;
    }

    .sponsorList {
        padding-top: 25px;
    }

    .sponsor {
        padding: 20px;
    }

    .sponsorImage {
        height: 6vh;
    }

    .partnerList {
        padding-bottom: 25px;
    }

    .partnerImage {
        height: 6vh;
    }
}

@media (max-width: 450px) {
    .membersList {
        padding: 2vw;
    }

    .memberImage {
        width: 70vw;
    }

    .memberName {
        font-size: 5vw;
    }

    .contactLink {
        font-size: 4vw;
        padding: 1vw;
    }

    .collaboratorName {
        font-size: 3.5vw;
    }

    .sponsorList {
        padding-top: 20px;
    }

    .sponsor {
        padding: 20px;
    }

    .sponsorImage {
        height: 5vh;
    }

    .partnerList {
        padding-bottom: 20px;
    }

    .partnerImage {
        height: 5vh;
    }

    .logoUB {
        width: 50vw;
        height: auto;
    }
}

