@media (max-width: 1500px) {
    .cards {
        justify-content: space-around;
    }

    .card {
        width: 450px;
    }
}

@media (max-width: 1200px) {
    .card {
        width: 400px;
    }
}

@media (max-width: 1100px) {
    #about-us .box {
        width: 90vw;
    }

    .card {
        width: 450px;
    }

    #hackathon .info {
        flex-direction: column;
    }

    #hackathon .info .detailsList {
        width: auto;
    }
}

@media (max-width: 768px) {
    #NavBar:not(.hidden) {
        position: relative;
        flex-direction: column;
        align-items: center;
        justify-content: flex-start;
        background: var(--dark2);
        z-index: 10;
        width: 100vw;
    }

    .box {
        padding: 6vw;
    }

    .title {
        font-size: 8vw;
    }

    #subtitle, .subtitle {
        font-size: 4vw;
    }

    .button-link {
        font-size: 2.5vw;
    }

    #hackathon h4 {
        font-size: 3vw;
    }

    #workshops .date {
        font-size: 3vw;
    }

    .inviteCards .card {
        flex-direction: column;
        width: 450px;
    }

    .inviteCards .card-title-section {
        width: auto;
    }

    .vertical {
        display: none;
    }

    .horizontal {
        display: inline-block;
    }

    #conferences h4 {
        font-size: 3vw;
    }

    #winners h4 {
        font-size: 3vw;
    }

    .vertical-timeline-element-title {
        font-size: 20px;
    }

    .vertical-timeline-element-content p {
        font-size: 15px;
    }

    #partners h4 {
        font-size: 3vw;
        padding: 2vw;
        text-align: center;
    }
}

@media (max-width: 650px) {
    .inviteCards .card {
        width: 80vw;
    }

    .winners-podium {
        display: flex;
        flex-direction: column;
    }

    .gray1, .gray2, .gray3 {
        display: none;
    }

    #contact {
        justify-content: center;
    }

    .sponsorList {
        padding-top: 25px;
    }

    .sponsor {
        padding: 20px;
    }

    .sponsorImage {
        height: 6vh;
    }

    .partnerList {
        padding-bottom: 25px;
    }

    .partnerImage {
        height: 6vh;
    }
}

@media (max-width: 450px) {
    .membersList {
        padding: 2vw;
    }

    .memberImage {
        width: 70vw;
    }

    .memberName {
        font-size: 5vw;
    }

    .contactLink {
        font-size: 4vw;
        padding: 1vw;
    }

    .collaboratorName {
        font-size: 3.5vw;
    }

    .sponsorList {
        padding-top: 20px;
    }

    .sponsor {
        padding: 20px;
    }

    .sponsorImage {
        height: 5vh;
    }

    .partnerList {
        padding-bottom: 20px;
    }

    .partnerImage {
        height: 5vh;
    }

    .logoUB {
        width: 50vw;
        height: auto;
    }
}
