#about-us {
    background-color: var(--dark2);
    color: var(--light2);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 10px;
}

#about-us .box {
    background-color: var(--accent);
    box-shadow: 0 4px 0 0 var(--light2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 70vw;
}

#about-us .title {
    font-size: 2.5rem;
}

#about-us ul {
    padding-top: 2vw;
    font-size: 1.2rem;
}

#about-us li {
    padding: 1vw;
}
