#home {
    background: url('../../images/fundal-home-page.jpg') no-repeat center center fixed;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
}

#home .box {
    padding: 3rem;
    background-color: white;
    box-shadow: 0 4px 8px 0 var(--accent), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    border-radius: 2px;
}

#home .title {
    text-align: left;
}

#subtitle {
    font-size: 2rem;
    text-align: center;
}

#first-page.button {
    background: var(--accent);
}
