#hackathon {
    background-color: var(--light2);
    display: flex;
}

#hackathon .container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#hackathon .info {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

#hackathon .info .detailsList {
    width: 25vw;
}

.detailsList {
    padding-top: 25px;
    padding-bottom: 25px;
}

#hackathon .detailsTitle {
    text-align: center;
}

#hackathon .button {
    justify-content: flex-start;
    margin: 0;
}

#hackathon .button-link:not(:hover) {
    background-color: var(--accent);
    color: var(--light2);
}
