#schedule {
    background: var(--dark2);
    display: flex;
    flex-direction: column;
    padding-left: 10px;
    padding-right: 10px;
}

#schedule .title {
    color: var(--light2);
    padding-bottom: 5vh;
    text-transform: uppercase;
}

#schedule .date {
    color: var(--light2);
}

.vertical-timeline {
    padding-top: 5vh;
}

.scheduleIcon {
    background: var(--schedule-color-1);
    color: var(--light2);
}
