#partners {
    background-color: var(--light2);
    color: var(--dark2);
    display: flex;
    flex-direction: column;
    justify-content: center;
    letter-spacing: 2px;
    margin-left: 50px;
    margin-right: 50px;
}

#partners .title {
    text-transform: uppercase;
}

.sponsorList {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    padding-top: 50px;
}

.sponsor {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 30px;
}

.sponsorImage {
    height: 70px;
    width: auto;
}

.partnerList {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    padding-bottom: 50px;
}

.partnerImage {
    height: 55px;
    width: auto;
}

#partners .button {
    margin: 10px;
    padding: 2px;
}
